body{
    background-color: rgba(255, 255, 255, 0.897);
}
li{
    text-decoration: none;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    
    
    
}

.container_flex{
    display: flex;
    flex-direction: row;
    border: 1px solid #000;
    flex-wrap: wrap;
}
.Buttons{
    background-color: white;
    border-radius: 3px;
    width: 600px;
    height: 80px;
    margin: 10px;
    border: solid .5px rgba(0, 0, 0, 0.349);
    box-shadow: 0 0 0 1px rgb(63 63 68 / 5%), 0 1px 3px 0 rgb(63 63 68 / 15%);
    transition-timing-function: ease;
    transition-duration: .8s;
    cursor: pointer;
    
}

.Buttons:hover{
    background-color: rgba(160, 158, 158, 0.774);
}

.Buttons a{
    color: #000;
    margin: 0 auto;
    width: 100%;
    text-align: center;
    vertical-align: center;
    padding:  5% 0;
    text-decoration: none;
    
}